/*-----------------------------------------------
|   Theme Styles
-----------------------------------------------*/
/*-----------------------------------------------
|   Reboot
-----------------------------------------------*/
::placeholder {
  opacity: 1;
  color: var(--reign-400);
}

::selection {
  background-color: #dc3545;
  color: #fff;
}

html {
  scroll-padding-top: 5.625rem;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  min-height: 100vh;
}

pre,
code {
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
}

section {
  position: relative;
  padding-top: 5rem;
  padding-bottom: 5rem;
  scroll-margin-top: -1.5625rem;
}
@media (min-width: 768px) {
  section {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }
}

input,
button,
select,
optgroup,
textarea,
label,
.alert,
.badge,
.blockquote-footer,
.btn,
.navbar,
.pagination,
.valid-feedback,
.invalid-feedback {
  font-family: Manrope, Arial, serif;
}

pre {
  margin: 0;
  transform: translate3d(0, 0, 0);
}

strong {
  font-weight: 600;
}

label {
  font-weight: 500 !important;
  margin-bottom: 0.625rem;
}

pre,
code,
kbd,
samp {
  font-size: 0.875rem !important;
}

ol {
  padding-left: 3.125rem;
}

small, .small {
  font-weight: 300 !important;
}

